.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
}

.twitter-logo {
  height: 10vmin;
  pointer-events: none;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-state {
  position: absolute;
  left: 0;
  width: 100%;
  transform: translateY(-50%);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.stateSwitcherTransition-enter {
  opacity: 0;
}
.stateSwitcherTransition-enter-active {
  opacity: 1;
  transition: opacity .5s;
}
.stateSwitcherTransition-exit {
  opacity: 1;
}
.stateSwitcherTransition-exit-active {
  opacity: 0;
  transition: opacity .5s;
}

.App-trend {
  position: relative;
  display: flex;
  flex-wrap: wrap;
}

.App-tweet {
  margin-right: 1px;
  margin-bottom: 1px;
  width: 50%;
  overflow: auto;
}